import { A as ADMIN_EMAIL } from "../../../../chunks/private.js";
import { e as error } from "../../../../chunks/index.js";
import { s as supabase } from "../../../../chunks/supabaseClient.js";
const load = async ({ cookies }) => {
  const emailcookie = cookies.get("useremail");
  if (emailcookie === ADMIN_EMAIL) {
    return { emailcookie };
  }
  error(403, "Forbidden");
};
const actions = {
  default: async ({ request, cookies }) => {
    const formData = await request.formData();
    const title = formData.get("title")?.toString();
    const body = formData.get("body")?.toString();
    if (title && body) {
      if (cookies.get("useremail") === ADMIN_EMAIL) {
        const { error: error2 } = await supabase.from("blog_posts").insert({
          title,
          body
        });
        if (error2) {
          console.log(error2);
        }
      } else {
        error(403, "Forbidden");
      }
    }
    return { success: true };
  }
};
export {
  actions,
  load
};
